<template>
  <div class="container">
    <div class="container__content">

      <header class="container__header">
        <div class="container__title">
          <i class="fa fa-tasks fa-3x"></i>
          <h1>My Todo-s</h1>
        </div>

        <form class="container__form">
          <div class="container__pick">
            <input class="container__input" type="text" v-model="newTodo" placeholder="Crie uma nova tarefa..."/>

          </div>
          <button class="container__add" @click="addTodo()">Add a Todo</button>
        </form>


                <datepicker/>
                <!-- <button @click="openPicker">Open Picker</button> -->

        
        <div class="container__options">
          <ul>
            <li><a href="#" @click.prevent="visibility='all'"  :class="{ selected: visibility == 'all' }">All</a></li>
            <li><a href="#" @click.prevent="visibility='active'"  :class="{ selected: visibility == 'active' }">Active</a></li>
            <li><a href="#" @click.prevent="visibility='completed'"  :class="{ selected: visibility == 'completed' }">Completed</a></li>
          </ul>
        </div>



      </header>

      <main class="container__main">
        <ul class="container__list">
          <li v-for="todo in todosOptions" v-bind:key="todo" v-bind:todo="todo">
            <div class="container__item" >
              <div class="container__checkbox">
                <input type="checkbox" @click="completeTodo(todo)" id="check-input" v-model="todo.isCompleted">
                <label for="check-input" ></label>
              </div>

              <div  class="container__description">
                <p :class="{ completed: todo.isCompleted}">{{ todo.description }}</p>
              </div>
              <div class="container__modifiers">
                <button @click="editTagsTodo(todo)">
                  <i id="tags" class="fas fa-tags fa-2x"></i>
                </button>
                <button @click="editTodo(todo)">
                  <i id="edit" class="fa fa-pencil text-info btn m-0 p-0 fa-2x" data-toggle="tooltip" data-placement="bottom" title="Edit todo"></i>
                </button>
                <button @click="deleteTodo(todo)">
                  <i id="delete" class="fa fa-trash-o text-danger btn m-0 p-0 fa-2x" data-toggle="tooltip" data-placement="bottom" title="Delete todo"></i>
                </button>
              </div>
            </div>
          </li>
        </ul>
      </main>
    </div>
  </div>
</template>

<script>
import datepicker from 'vue3-datepicker'


export default {
  name: 'TodoList',
  data() {
    return {
      todos: [],
      newTodo: null,
      visibility: 'all'
    };
  },

  created() {
    this.todos = JSON.parse(localStorage.getItem("todo-storage") || '[]')
  },

  computed: {
    todosOptions() {
      if (this.visibility === 'all') {
        return this.todos
      }
      else if (this.visibility === 'active') {
        return this.todos.filter( (todo) => {
            return !todo.isCompleted
        } )
      }
      else {
        return this.todos.filter( (todo) => {
          return todo.isCompleted
        })
      }
    }
  },

  methods: {
    addTodo() {
      this.newTodo = this.newTodo.trim()
      if (this.newTodo && this.newTodo.length != 0) {
        this.todos.push({
          id: this.todos.length,
          description: this.newTodo,
          isCompleted: false
        })

        this.newTodo = ''
        localStorage.setItem("todo-storage", JSON.stringify(this.todos))
      }
    },

    deleteTodo(todo) {
      this.todos.splice(this.todos.indexOf(todo), 1)
      localStorage.setItem("todo-storage", JSON.stringify(this.todos))
    },

    completeTodo(todo) {
      todo.isCompleted = !(todo.isCompleted)
      console.log(todo)
      localStorage.setItem("todo-storage", JSON.stringify(this.todos))
    }
  },

components: { datepicker }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style src='./style.scss' lang='scss' scoped />




